* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
}

.container {
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  padding: 50px;
  max-width: 500px;
  width: 100%;
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 32px;
  color: #333;
  margin-bottom: 10px;
  font-weight: 700;
}

.header p {
  font-size: 16px;
  color: #666;
}

.input-group {
  margin-bottom: 30px;
}

.file-input-wrapper {
  position: relative;
  display: inline-block;
  width: 100%;
  cursor: pointer;
}

.file-input-wrapper input[type="file"] {
  display: none;
}

.file-input-label {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  border: 2px dashed #667eea;
  border-radius: 12px;
  background: #f8f9ff;
  color: #667eea;
  font-weight: 600;
  font-size: 16px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.file-input-label:hover {
  background: #667eea;
  color: white;
  border-color: #667eea;
}

.file-name {
  display: block;
  margin-top: 10px;
  font-size: 14px;
  color: #667eea;
  font-weight: 500;
}

.button-group {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
}

.upload-btn {
  flex: 1;
  padding: 15px 30px;
  font-size: 16px;
  font-weight: 600;
  color: white;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.upload-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

.upload-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.clear-btn {
  padding: 15px 30px;
  font-size: 16px;
  font-weight: 600;
  color: #667eea;
  background: white;
  border: 2px solid #667eea;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.clear-btn:hover {
  background: #f0f2ff;
}

.result-container {
  background: #f0f2ff;
  border-radius: 12px;
  padding: 30px;
  text-align: center;
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.result-label {
  font-size: 16px;
  color: #666;
  margin-bottom: 10px;
}

.result-value {
  font-size: 48px;
  font-weight: 700;
  color: #667eea;
  margin-bottom: 10px;
}

.result-confidence {
  font-size: 14px;
  color: #999;
  font-style: italic;
}

.confidence-score {
  margin-top: 12px;
  font-size: 13px;
  color: #667eea;
  font-weight: 600;
  font-style: normal;
}

.loading {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.error-message {
  background: #fee;
  color: #c33;
  padding: 15px;
  border-radius: 8px;
  margin-top: 15px;
  font-size: 14px;
  border-left: 4px solid #c33;
}

@media (max-width: 600px) {
  .container {
    padding: 30px;
  }

  .header h1 {
    font-size: 24px;
  }

  .file-input-label {
    padding: 20px;
    font-size: 14px;
  }

  .result-value {
    font-size: 36px;
  }

  .button-group {
    flex-direction: column;
  }
}
